import request from '@/utils/request'

// ÂàóË°®
export function getArticleList(pageNo, size, type) {
  return request(({
    url: '/article/list',
    method: 'get',
    params: { pageNo: pageNo, size: size, type: type }
  }))
}

// ÂèëÂ∏É
export function postArticle(topic) {
  return request({
    url: '/article/create',
    method: 'article',
    data: topic
  })
}

// ÊµèËßà
export function getArticle(id) {
  return request({
    url: '/article',
    method: 'get',
    params: {
      articleId: id
    }
  })
}

// Ëé∑ÂèñËØ¶ÊÉÖÈ°µÊé®Ëçê
export function getRecommendTopics(id) {
  return request({
    url: '/article/recommend',
    method: 'get',
    params: {
      articleId: id
    }
  })
}

export function updateArticle(topic) {
  return request({
    url: '/article/update',
    method: 'article',
    data: topic
  })
}

export function deleteArticle(id) {
  return request({
    url: `/article/delete/${id}`,
    method: 'delete'
  })
}

